import { send } from "@emailjs/browser";
import { Grid, TextField, Button, Typography } from "@mui/material";
import { Formik, Form } from "formik";
import { useSnackbar } from "notistack";
import { EnquiryFormLabel, EnquiryFormName, EnquiryFormProps } from "./utils";
import type { FormikConfig, FormikErrors } from "formik";

const EmailJSKeys = {
  serviceID: "service_u52pzd8",
  templateID: "template_3uy65wi",
  publicID: "JGMR1-eH-smwxh7rB",
};

// Adopted from App repo. Ref: https://github.com/manufac-analytics/app/blob/9c8c76f79376391ff9ccb924a8d684bf1df91ba3/packages/forms/src/components/Contact-Form/index.tsx#L36
const validateHandler: FormikConfig<EnquiryFormProps>["validate"] = (values) => {
  let formikErrors: FormikErrors<EnquiryFormProps> = {};
  const parsedData = EnquiryFormProps.safeParse(values);
  if (parsedData.success === false) {
    formikErrors = parsedData.error.errors.reduce<FormikErrors<EnquiryFormProps>>((curr, error) => {
      return {
        ...curr,
        [error.path.toString()]: error.message,
      };
    }, {});
  }

  return formikErrors;
};

export function EnquiryForm(props: EnquiryFormProps): JSX.Element {
  const { enqueueSnackbar } = useSnackbar();
  const { serviceID, publicID, templateID } = EmailJSKeys;

  const onSubmitHandler: FormikConfig<EnquiryFormProps>["onSubmit"] = async (
    values,
    { resetForm }
  ) => {
    try {
      let templateParams = values;
      const data = EnquiryFormProps.safeParse(values);
      if (data.success === true) {
        templateParams = data.data;
      }
      await send(serviceID, templateID, templateParams, publicID);
      enqueueSnackbar("Enquiry sent successfully.");
      resetForm();
    } catch (err) {
      if (err instanceof Error) {
        enqueueSnackbar(`Error on sending enquiry: ${err.message}`);
      }
    }
  };

  return (
    <Formik initialValues={props} onSubmit={onSubmitHandler} validate={validateHandler}>
      {({ values, handleChange, handleBlur, isSubmitting, errors, touched }) => (
        <Form style={{ width: "100%" }}>
          <Grid container spacing={2} direction="column">
            <Grid item>
              <Typography variant="h5" gutterBottom>
                Contact Us
              </Typography>
              <Typography variant="body2">
                Send us a message. We will get back to you as soon as possible.
              </Typography>
            </Grid>
            <Grid item>
              <TextField
                variant="outlined"
                name={EnquiryFormName.name}
                label={EnquiryFormLabel.name}
                placeholder="Enter full name"
                required
                fullWidth
                value={values.name}
                onChange={handleChange}
                onBlur={handleBlur}
                error={errors.name !== undefined && touched.name === true}
                helperText={errors.name !== undefined && touched.name === true ? errors.name : ""}
              />
            </Grid>
            <Grid item>
              <TextField
                variant="outlined"
                name={EnquiryFormName.company}
                label={EnquiryFormLabel.company}
                placeholder="Enter company's name"
                required
                fullWidth
                value={values.company}
                onChange={handleChange}
                onBlur={handleBlur}
                error={errors.company !== undefined && touched.company === true}
                helperText={
                  errors.company !== undefined && touched.company === true ? errors.company : ""
                }
              />
            </Grid>
            <Grid item>
              <TextField
                variant="outlined"
                name={EnquiryFormName.email}
                label={EnquiryFormLabel.email}
                placeholder="Enter email"
                required
                fullWidth
                value={values.email}
                onChange={handleChange}
                onBlur={handleBlur}
                error={errors.email !== undefined && touched.email === true}
                helperText={
                  errors.email !== undefined && touched.email === true ? errors.email : ""
                }
              />
            </Grid>
            <Grid item>
              <TextField
                variant="outlined"
                name={EnquiryFormName.phone}
                label={EnquiryFormLabel.phone}
                placeholder="Enter phone number"
                fullWidth
                value={values.phone}
                onChange={handleChange}
                onBlur={handleBlur}
                error={errors.phone !== undefined && touched.phone === true}
                helperText={
                  errors.phone !== undefined && touched.phone === true ? errors.phone : ""
                }
              />
            </Grid>
            <Grid item>
              <TextField
                variant="outlined"
                name={EnquiryFormName.message}
                label={EnquiryFormLabel.message}
                placeholder="Enter your message here"
                multiline
                rows={4}
                fullWidth
                required
                value={values.message}
                onChange={handleChange}
                onBlur={handleBlur}
                error={errors.message !== undefined && touched.message === true}
                helperText={
                  errors.message !== undefined && touched.message === true ? errors.message : ""
                }
              />
            </Grid>
            <Grid container item justifyContent="flex-end">
              <Grid item sm={4} xs={12}>
                <Button variant="contained" fullWidth type="submit" disabled={isSubmitting}>
                  Send
                </Button>
              </Grid>
            </Grid>
          </Grid>
        </Form>
      )}
    </Formik>
  );
}
