import { Theme } from "@mui/material";
import { Us, Sa, Ru, De, Cn } from "react-flags-select";
import type { SxProps } from "@mui/material";
import type { SetStateAction, Dispatch } from "react";
import type { UseTranslationResponse } from "react-i18next";

interface Language {
  icon: JSX.Element;
  longForm: string;
}

export interface CustomAppBarProps {
  darkMode: boolean;
  setDarkMode: Dispatch<SetStateAction<boolean>>;
  language: string;
  changeLanguage: UseTranslationResponse<string>[1]["changeLanguage"];
}

export const LanguageDictionary: Record<string, Language> = {
  en: { icon: <Us />, longForm: "English" },
  ar: { icon: <Sa />, longForm: "Arabic" },
  ru: { icon: <Ru />, longForm: "Russian" },
  de: { icon: <De />, longForm: "German" },
  zh: { icon: <Cn />, longForm: "Chinese" },
};

export const RoutePathList = {
  Home: "/",
  "About Us": "/about",
  Products: "/products",
  Manufacturing: "/manufacturing",
  Exports: "/exports",
  "Contact Us": "/contact",
};

export const ActiveStyle: SxProps<Theme> = {
  "&.active": {
    fontWeight: 600,
  },
};

/**
 * When language is detected as ISO code such as 'en-US',
 * converting it to key of the language in 'LanguageDictionary'.
 * @param isoForm
 * @returns string
 */
export function getLanguageShortForm(isoForm: string): string {
  return isoForm.split("-")[0];
}
