import {
  Typography,
  ThemeProvider,
  createTheme,
  CssBaseline,
  useMediaQuery,
  Direction,
} from "@mui/material";
import { SnackbarProvider } from "notistack";
import { useEffect, useMemo, useState } from "react";
import { useTranslation } from "react-i18next";
import { Outlet } from "react-router-dom";
import { CustomAppBar } from "./components/CustomAppBar";
import { CustomMobileAppBar } from "./components/CustomMobileAppBar";
import { getLanguageShortForm } from "./components/utils";

function getTheme(isDark: boolean, direction: Direction) {
  return createTheme({
    palette: {
      mode: isDark === true ? "dark" : "light",
    },
    direction,
  });
}

export function App(): JSX.Element {
  const {
    t,
    i18n: { language, dir, changeLanguage },
  } = useTranslation();
  const docDir = dir();
  const [darkMode, setDarkMode] = useState(false);
  const theme = useMemo(() => {
    return getTheme(darkMode, docDir);
  }, [darkMode, docDir]);
  const matches = useMediaQuery(theme.breakpoints.down("lg"));
  useEffect(() => {
    document.dir = docDir;
  }, [docDir]);

  return (
    <ThemeProvider theme={theme}>
      <SnackbarProvider>
        <CssBaseline />
        {matches === true ? (
          <CustomMobileAppBar
            darkMode={darkMode}
            setDarkMode={setDarkMode}
            language={getLanguageShortForm(language)}
            changeLanguage={changeLanguage}
          />
        ) : (
          <CustomAppBar
            darkMode={darkMode}
            setDarkMode={setDarkMode}
            language={getLanguageShortForm(language)}
            changeLanguage={changeLanguage}
          />
        )}
        <Typography>
          Language: {language} | {t("Welcome")}
        </Typography>
        <Outlet />
      </SnackbarProvider>
    </ThemeProvider>
  );
}
