import { Brightness7Rounded, Brightness4Rounded, Menu } from "@mui/icons-material";
import {
  AppBar,
  Toolbar,
  Stack,
  Box,
  MenuItem,
  ListItemText,
  ListItemIcon,
  Select,
  IconButton,
  Typography,
  Divider,
  Drawer,
  List,
  ListItemButton,
  Grid,
} from "@mui/material";
import { useState } from "react";
import { NavLink, useLocation } from "react-router-dom";
import { LanguageDictionary, RoutePathList, ActiveStyle } from "../utils";
import type { CustomAppBarProps } from "../utils";
import type { SelectProps } from "@mui/material";

export function CustomMobileAppBar({
  darkMode,
  setDarkMode,
  language,
  changeLanguage,
}: CustomAppBarProps): JSX.Element {
  const [drawerOpen, setDrawerOpen] = useState(false);
  const { pathname } = useLocation();

  const handleLanguageChange: SelectProps<string>["onChange"] = (event) => {
    const { value } = event.target;
    changeLanguage(value);
  };

  const onRenderValue: SelectProps<string>["renderValue"] = (val) => {
    return <IconButton>{LanguageDictionary[val].icon}</IconButton>;
  };

  const handleToggleDarkMode = () => {
    setDarkMode((previousState) => !previousState);
  };

  const handleDrawerToggle = () => {
    setDrawerOpen((prev) => !prev);
  };

  const handleDrawerClose = () => {
    setDrawerOpen(false);
  };

  return (
    <>
      <AppBar position="static">
        <Toolbar>
          <Box
            component="img"
            alt="logo"
            src="https://manufac-analytics-images.s3.ap-south-1.amazonaws.com/logos/landscape/black/Manufac_Final-2.png"
            sx={{ width: "150px" }}
          />
          <Stack direction="row" spacing={1} justifyContent="flex-end" sx={{ width: "100%" }}>
            <Select
              value={language}
              onChange={handleLanguageChange}
              variant="outlined"
              renderValue={onRenderValue}
              sx={{ height: "40px" }}
            >
              {Object.entries(LanguageDictionary).map(([shortForm, { icon, longForm }]) => (
                <MenuItem value={shortForm} key={shortForm}>
                  <ListItemIcon>{icon}</ListItemIcon>
                  <ListItemText>{longForm}</ListItemText>
                </MenuItem>
              ))}
            </Select>
            <IconButton
              title={darkMode === true ? "Switch to Light Mode" : "Switch to Dark Mode"}
              onClick={handleToggleDarkMode}
            >
              {darkMode === true ? <Brightness7Rounded /> : <Brightness4Rounded />}
            </IconButton>
            <IconButton onClick={handleDrawerToggle}>
              <Menu />
            </IconButton>
          </Stack>
        </Toolbar>
      </AppBar>
      <Drawer variant="temporary" open={drawerOpen} onClose={handleDrawerClose}>
        <Grid
          container
          direction="column"
          onClick={handleDrawerClose}
          spacing={1}
          alignItems="center"
          marginTop={1}
        >
          <Grid item>
            <Typography variant="h6">JUGAL</Typography>
          </Grid>
          <Grid item>
            <Divider />
          </Grid>
          <Grid item>
            <List dense>
              {Object.entries(RoutePathList).map(([name, route]) => (
                <ListItemButton
                  component={NavLink}
                  to={route}
                  color="inherit"
                  sx={route === pathname ? ActiveStyle : undefined}
                >
                  {name}
                </ListItemButton>
              ))}
            </List>
          </Grid>
        </Grid>
      </Drawer>
    </>
  );
}
